import React from "react";
import { Stack, Typography, Link, Button } from "@mui/material";

import { useTranslation } from "react-i18next";

export default function SummerPlans() {
  const { t, i18n } = useTranslation();
  return (
    <>
      <Stack sx={{ marginBottom: 2 }}>
        <Typography variant="h3" gutterBottom={true}>
          {t("summerPlans.header")}
        </Typography>
        <Stack spacing={2}>
          <Typography variant="body2">
            <strong>{t("summerPlans.jun")}</strong>
            {t("summerPlans.junText")}
          </Typography>
          <Typography variant="body2">
            <strong>{t("summerPlans.jul")}</strong>
            {t("summerPlans.julText")}
          </Typography>
          <Typography variant="body2">
            <strong>{t("summerPlans.aug")}</strong>
            {t("summerPlans.augText1")}{" "}
            <Link href="https://ton.org/ru/grants">
              {t("summerPlans.augLink1")}
            </Link>
            {t("summerPlans.augText2")}{" "}
            <Link href="https://ston.fi/stonbassadors-ru">
              {t("summerPlans.augLink2")}
            </Link>
          </Typography>
          <Typography variant="body2">
            {t("summerPlans.additional1")}
          </Typography>
          <Typography variant="body2">
            {t("summerPlans.additional2")} 
          </Typography>
        </Stack>
      </Stack>
      <Button
        component="a"
        target="_blank"
        href="https://app.ston.fi/swap?chartVisible=false"
        variant="contained"
        sx={{ width: "100%", marginBottom: 2 }}
      >
        {t("summerPlans.buyButton")}
      </Button>
    </>
  );
}
